import axios from "axios";
import store from '../store';
import {getBfmRequestObject, getPnrRequestObject} from "./SabreData.js";
import {processSearchFlights, isLocalHost} from '../api/utils.js';

let AccessToken = "";

const sabreLiveApi = 'https://api.havail.sabre.com';
const sabreTestApi = 'https://api-crt.cert.havail.sabre.com';

const SabreAuthHeaderLive= "VmpFNk5EZ3lNRFU0T2toVE5VbzZRVUU9OlVrVklRVTR4TWc9PQ==";
const SabreAuthHeaderTest ="VmpFNk5EZ3lNRFU0T2toVE5VbzZRVUU9OlUxUkJVbFJTTlE9PQ==";

let sabreBaseURL = sabreTestApi;
let sabreAuthHeader = SabreAuthHeaderTest;

let useLiveApi = process.env.NODE_ENV === 'production';
//let useLiveApi = true;

async function verifyAuthToken() {
    AccessToken = "";
    if(useLiveApi){
        //Use Live API
        //console.log("using live sabre api");
        sabreBaseURL = sabreLiveApi;
        sabreAuthHeader = SabreAuthHeaderLive;
        let liveToken = store.getters.getAuthToken;
        if (liveToken === "") {
            let token = await getAuthToken();
            store.commit('setAuthToken', token);
            AccessToken = token;
        }else {
            AccessToken = liveToken;
        }
    }else {
        //Use Test API
        console.log("using test sabre api");
        sabreBaseURL = sabreTestApi;
        sabreAuthHeader = SabreAuthHeaderTest;
        let testToken = store.getters.getTestToken;
        if (testToken === "") {
            let token = await getAuthToken();
            store.commit('setTestToken', token);
            AccessToken = token;
        }else {
            AccessToken = testToken;
        }
    }
    //console.log("Bareer Token");
    //console.log(AccessToken);
}

//#region external API calls
export async function getAuthToken() {
    //console.log("get auth token from "+ sabreBaseURL + " using auth Header "+ sabreAuthHeader);
    return await axios.post(`${sabreBaseURL}/v2/auth/token`, "grant_type=client_credentials", {
        headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            "Authorization": `Basic ${sabreAuthHeader}`
        }
    }).then(function (response) {
        //console.log("Token Request Response");
        //console.log(JSON.stringify(response));
        if (response.status === 200) {
            return response.data.access_token;
        }
    }).catch(function (error) {
        try {
            if (error.response) {
                console.error("Error details");
                console.error(error.response.data);
            } else {
                console.error(error);
            }
        } catch (e) {
            console.error(e);
        }
        return "";
    });
}

export async function searchFlights(params) {
    await verifyAuthToken();
    let requestData = getBfmRequestObject(params);
    //console.log(`BFM request data sent to: >> ${sabreBaseURL}/v2/offers/shop`);
    //console.log(JSON.stringify(requestData));
    return await axios.post(`${sabreBaseURL}/v2/offers/shop`, requestData, {
        "headers": {
            "Content-Type": "application/json",
            "Authorization": `Bearer ${AccessToken}`
        }
    }).then((response) => {
        //console.log("BFM Request Response");
        //console.log(JSON.stringify(response));
        // Handle success
        if (response.status === 200) {
            let d = response.data;
            if (d.groupedItineraryResponse.statistics.itineraryCount > 0) {
                return processSearchFlights(d.groupedItineraryResponse);
            } else {
                //console.log(d.groupedItineraryResponse.messages);
                //console.log("itinerary count is zero");
            }
            return [];
        }
    }).catch((error) => {
        try {
            if (error.response) {
                console.error("Error details");
                console.error(error.response.data);
            } else {
                console.error(error);
            }
        } catch (e) {
            console.error(e);
        }
        return []
    }).finally(() => {
        //Perform action in always
    });
}

export async function createPNR(params) {
    let pnrObject = getPnrRequestObject(params);
    /*console.log(`PNR request data sent to: >> ${sabreBaseURL}/v2.4.0/passenger/records?mode=create`);
    console.log("PNR Request data");
    console.log(JSON.stringify(pnrObject));*/
    if(isLocalHost){
        return { status: true, data: null };
    }
    await verifyAuthToken();
    return await axios.post(`${sabreBaseURL}/v2.4.0/passenger/records?mode=create`, pnrObject, {
        "headers": {
            "Content-Type": "application/json",
            "Authorization": `Bearer ${AccessToken}`
        }
    }).then((response) => {
        // Handle success//Complete
        let isSuccessFull = false;
        if(response.status === 200) {
            if(response.data.CreatePassengerNameRecordRS.ApplicationResults.status.toLowerCase() === "complete") {
                isSuccessFull = true;
                console.log("PNR Created Successfully.");
            }
        }
        let detailMessage = "";
        if(!isSuccessFull){
            let errorsText = "";
            let waringsText = "";
            response.data.CreatePassengerNameRecordRS.ApplicationResults.Error.forEach(function (e) {
                e.SystemSpecificResults.forEach(function (ssr) {
                    ssr.Message.forEach(function (m) {
                        errorsText+= `<span>CODE: ${m.code} <br/> ${m.content}<span>`
                    });
                });
            });
            response.data.CreatePassengerNameRecordRS.ApplicationResults.Warning.forEach(function (e) {
                e.SystemSpecificResults.forEach(function (ssr) {
                    ssr.Message.forEach(function (m) {
                        waringsText+= `<span>CODE: ${m.code} <br/> ${m.content}<span>`
                    });
                });
            });

            detailMessage = `<span><b>Errors: </b>${errorsText}</span><span><b>Warnings: </b>${waringsText}</span>`;
            console.log("Failed to create PNR.");
            console.log("user data ");
            console.log(JSON.stringify(params));
            console.log("PNR Request data");
            console.log(JSON.stringify(pnrObject));
            console.log("PNR Response data");
            console.log(JSON.stringify(response.data));
        }
        return { status: isSuccessFull, data: response.data, message: detailMessage };
    }).catch((error) => {
        console.error("Error details");
        console.error(JSON.stringify(error));
        return { status: false, data: null };
    }).finally(() => {
        //Perform action in always
    });
}

//#endregion

export async function getLocalFlightsData() {
    return await axios.get("/meta-data/flights/bfm-response-arrives-next-day-606.json").then((response) => {
        // Handle success
        return processSearchFlights(response.data.groupedItineraryResponse);
    }).catch((error) => {
        console.error("An error occurred.");
        console.error(error);
        return [];
    }).finally(() => {
        //Perform action in always
    });
}

