<template>
    <section class="booking-page">
        <div class="container" v-if="flightData.summary !== undefined">
            <div class="row">
                <div class="col-sm-9">
                    <div class="flight-details">
                        <div class="flight-list-item" v-bind:id="'item_'+flightData.id">
                            <div class="col-sm-12 legs-row">
                                <div class="row">
                                    <div class="col-sm-2">
                                        <img class="airline-logo"
                                             v-bind:src="'images/flights/'+flightData.FlightCode+'.png'"
                                             v-bind:alt="getFlightName(flightData.FlightCode)"/>
                                    </div>
                                    <div class="col-sm-9 itinerary-summary bg-light">
                                        <div class="row" v-for="(leg, legIndex) in flightData.summary.legs"
                                             :key="legIndex">
                                            <div class="col-sm-4 departure-column">
                                                <span class="info bold">{{ getAirportName(leg.depPoint.departureAirport)}}</span>
                                                <span class="info">
                                                    <i class="fa fa-plane"></i>
                                                    {{leg.depPoint.departureAirport}} - {{ getTime(leg.depPoint.departureDateTime)}}
                                                    </span>
                                                <span class="info no-wrap">{{ formatDate(leg.depPoint.departureDateTime) }}</span>
                                            </div>
                                            <div class="col-sm-4 stoppage-column">
                                                <span class="info">
                                                    <span class="info">
                                                        <span class="info goldy bold">{{getStopInfo(leg.stopNames)}}</span>
                                                        <span class="info separator">|</span>
                                                    </span>
                                                    <span class="info goldy bold">{{ getMinutesToString(leg.elapsedTime) }}</span>
                                                </span>
                                                <span v-if="leg.stopNames.length > 0">
                                                <span class="info stoppage"></span>
                                                <span class="info goldy bold">{{ leg.stopNames.join(',') }}</span>
                                            </span>
                                                <span v-else class="info stoppage none"></span>
                                            </div>
                                            <div class="col-sm-4 arrival-column">
                                                <span class="info bold">{{getAirportName(leg.arrPoint.arrivalAirport)}}</span>
                                                <span class="info">
                                                <i class="fa fa-plane fa-90"></i>
                                                {{leg.arrPoint.arrivalAirport}} - {{getTime(leg.arrPoint.arrivalDateTime)}}
                                                    </span>
                                                <span class="info no-wrap">{{ formatDate(leg.arrPoint.arrivalDateTime) }}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="col-sm-12 flight-detail-wrapper bg-light">
                                <div class="row">
                                    <div class="col-sm-4 col-5 first-column">
                                        <a data-toggle="collapse"
                                           v-bind:href="getUniqueId('#flightInformation',0,0)"
                                           role="button" aria-expanded="true"
                                           v-bind:aria-controls="getUniqueId('flightInformation',0,0)">
                                            <span class="first">Flight</span><span class="second">Hide</span>
                                            Details<i class="fa fa-chevron-down"></i>
                                        </a>
                                    </div>
                                    <div class="col-sm-4 col-2 text-center second-column">

                                    </div>
                                    <div class="col-sm-4 col-5 text-right third-column">

                                    </div>
                                </div>
                                <div class="row collapse show" v-bind:id="getUniqueId('flightInformation',0,0)">
                                    <div class="card card-body flight-information">
                                        <div class="leg-details" v-for="(leg, legIndexA) in flightData.legs"
                                             :key="legIndexA">
                                            <div class="row seg-details" v-for="(seg, rIndex) in leg.schedules"
                                                 :key="rIndex">
                                                <div class="col-sm col-12 column-a">
                                                    <span class="info bold">{{getFlightName(seg.airlineCode)}}</span>
                                                    <span class="info">
                                                    {{seg.airlineCode}}&nbsp;{{ seg.flightNumber }}
                                                </span>
                                                </div>
                                                <div class="col-sm col-12 column-b">
                                                    <span class="info">{{ getTime(seg.departureDateTime) }}</span>
                                                    <span class="info no-wrap">{{ formatDate(seg.departureDateTime) }}</span>
                                                    <span class="info">
                                                    {{ getAirportName(seg.departureAirport) }} ({{ seg.departureAirport }})
                                                </span>
                                                </div>
                                                <div class="col-sm col-12 column-c">
                                                <span class="info text-center">
                                                    <span class="info">{{ getMinutesToString(seg.elapsedTime) }}</span>
                                                    <span class="info goldy"><i class="fa fa-plane fa-45"></i></span>
                                                </span>
                                                </div>
                                                <div class="col-sm col-12 column-d">
                                                    <span class="info">{{ getTime(seg.arrivalDateTime) }}</span>
                                                    <span class="info no-wrap">{{ formatDate(seg.arrivalDateTime) }}</span>
                                                    <span class="info">{{ getAirportName(seg.arrivalAirport) }} ({{ seg.arrivalAirport }})</span>
                                                </div>
                                                <div class="col-sm col-12 column-e">
                                                    <span class="info">Baggage</span>
                                                    <span class="info"><i class="fa fa-briefcase"></i> {{flightData.baggage.bagWeight }}</span>
                                                </div>
                                                <div class="col-sm-12 lay-over"
                                                     v-html="getLayOver(leg.schedules, rIndex)"></div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-sm-3">
                    <div class="farebox">
                        <h3 class="farebox-heading">Fare Details</h3>
                        <div class="fare-detail">
                            <div class="row">
                                <div class="col-sm-5">
                                    <span class="info">
                                        Base Amount
                                    </span>
                                </div>
                                <div class="col-sm-7">
                                    <span class="info text-right">
                                    {{flightData.totalFare.currencyCode}}
                                    {{formatMoney(flightData.totalFare.baseFareAmount)}}
                                        </span>
                                </div>
                            </div>
                            <div class="row">
                                <div class="col-sm-5">
                                    <span class="info">Tax Amount</span>
                                </div>
                                <div class="col-sm-7">
                                    <span class="info text-right">
                                        {{flightData.totalFare.currencyCode}}
                                    {{formatMoney(flightData.totalFare.taxAmount)}}
                                    </span>
                                </div>
                            </div>
                            <hr/>
                            <div class="row">
                                <div class="col-sm-5">
                                    <span class="info bold">Total Fare</span>
                                </div>
                                <div class="col-sm-7">
                                    <span class="info bold text-right">
                                        {{flightData.totalFare.currencyCode}} {{
                                        formatMoney(flightData.totalFare.totalAmount) }}
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row">
                <div class="col-sm-12">
                    <form id="formPassengers" @submit.prevent="submitFormData">
                        <h2>Passengers </h2>
                        <hr/>
                        <div class="row" v-for="(i, adIndex) in adults" :key="adIndex">
                            <div class="col-sm-1 pr-0 form-group">
                                <label v-bind:for="getUniqueId('adGender', adIndex,0)">
                                    Gender
                                </label>
                                <select v-bind:id="getUniqueId('adGender', adIndex,0)"
                                        v-model="form.adults[adIndex].status" class="form-control" required>
                                    <option value="Mr">Mr</option>
                                    <option value="Mrs">Mrs</option>
                                    <option value="Ms">Ms</option>
                                    <option value="Miss">Miss</option>
                                    <option value="Mstr">Mstr</option>
                                </select>
                            </div>
                            <div class="col-sm-3 form-group">
                                <label v-bind:for="getUniqueId('adFirstName', adIndex,0)">
                                    First Name
                                </label>
                                <input v-bind:id="getUniqueId('adFirstName', adIndex,0)" type="text"
                                       placeholder="First Name"
                                       v-model="form.adults[adIndex].first_name" class="form-control" required>
                            </div>
                            <div class="col-sm-3 form-group">
                                <label v-bind:for="getUniqueId('adLastName', adIndex,0)">
                                    Last Name
                                </label>
                                <input v-bind:id="getUniqueId('adLastName', adIndex,0)" type="text"
                                       placeholder="Last Name"
                                       v-model="form.adults[adIndex].last_name" class="form-control" required>
                            </div>
                            <div class="col-sm-2 form-group">
                                <label v-bind:for="getUniqueId('adDob', adIndex,0)">
                                    DOB
                                </label>
                                <datepicker v-bind:id="getUniqueId('adDob', adIndex,0)" autocomplete="off"
                                            v-model="form.adults[adIndex].dob" @selected="getDDMMMYYYY"
                                            input-class="form-control" placeholder="DOB"
                                            :open-date="adultDobRange.from" :disabled-dates="adultDobRange"
                                            :format="getDDMMMYYYY" required></datepicker>
                            </div>
                            <div class="col-sm-3 form-group">
                                <label v-bind:for="getUniqueId('adNation', adIndex,0)">
                                    Nationality
                                </label>
                                <select v-bind:id="getUniqueId('adNation', adIndex,0)"
                                        v-model="form.adults[adIndex].nationality" class="form-control" required>
                                    <option value="">Select</option>
                                    <option v-for="(country, countryIndexA) in countries"
                                            v-bind:value="country.displayText" :key="countryIndexA">
                                        {{country.displayText}}
                                    </option>
                                </select>
                            </div>
                        </div>
                        <div v-if="childs > 0">
                            <hr/>
                            <h2>Childs</h2>
                            <div class="child-fields row" v-for="(i,chIndex) in childs" :key="chIndex">
                                <div class="col-sm-1 form-group">
                                    <label v-bind:for="getUniqueId('chGender', chIndex,0)">
                                        Gender
                                    </label>
                                    <select v-bind:id="getUniqueId('chGender', chIndex,0)"
                                            v-model="form.childs[chIndex].status" class="form-control" required>
                                        <option value="Mr">Mr</option>
                                        <option value="Mrs">Mrs</option>
                                        <option value="Ms">Ms</option>
                                        <option value="Miss">Miss</option>
                                        <option value="Mstr">Mstr</option>
                                    </select>
                                </div>
                                <div class="col-sm-3 form-group">
                                    <label v-bind:for="getUniqueId('chFirstName', chIndex,0)">
                                        First Name
                                    </label>
                                    <input v-bind:id="getUniqueId('chFirstName', chIndex,0)" type="text"
                                           placeholder="First Name"
                                           v-model="form.childs[chIndex].first_name" class="form-control" required>
                                </div>
                                <div class="col-sm-3 form-group">
                                    <label v-bind:for="getUniqueId('chLastName', chIndex,0)">
                                        Last Name
                                    </label>
                                    <input v-bind:id="getUniqueId('chLastName', chIndex,0)" type="text"
                                           placeholder="Last Name"
                                           v-model="form.childs[chIndex].last_name" class="form-control" required>
                                </div>
                                <div class="col-sm-2 form-group">
                                    <label v-bind:for="getUniqueId('chDob', chIndex,0)">
                                        DOB
                                    </label>
                                    <datepicker v-bind:id="getUniqueId('chDob', chIndex,0)"
                                                v-model="form.childs[chIndex].dob" autocomplete="off"
                                                input-class="form-control" placeholder="DOB"
                                                :open-date="chilDobRange.from" :disabled-dates="chilDobRange"
                                                @selected="getDDMMMYYYY"
                                                :format="getDDMMMYYYY" required></datepicker>
                                </div>
                                <div class="col-sm-3 form-group">
                                    <label v-bind:for="getUniqueId('chNation', chIndex,0)">
                                        Nationality
                                    </label>
                                    <select class="form-control" v-bind:id="getUniqueId('chNation', chIndex,0)"
                                            v-model="form.childs[chIndex].nationality" required>
                                        <option value="">Select</option>
                                        <option v-for="(country, countryIndexB) in countries"
                                                v-bind:value="country.displayText" :key="countryIndexB">
                                            {{country.displayText}}
                                        </option>
                                    </select>
                                </div>
                            </div>
                        </div>
                        <div v-if="infants > 0">
                            <h2>Infants</h2>
                            <hr/>
                            <div class="infant-fields row" v-for="(i,infIndex) in infants" :key="infIndex">
                                <div class="col-sm-1 pr-0 form-group">
                                    <label v-bind:for="getUniqueId('infGender', infIndex,0)">
                                        Gender
                                    </label>
                                    <select class="form-control" v-model="form.infants[infIndex].status"
                                            v-bind:id="getUniqueId('infGender', infIndex,0)" required>
                                        <option value="Mr">Mr</option>
                                        <option value="Mrs">Mrs</option>
                                        <option value="Ms">Ms</option>
                                        <option value="Miss">Miss</option>
                                        <option value="Mstr">Mstr</option>
                                    </select>
                                </div>
                                <div class="col-sm-3 form-group">
                                    <label v-bind:for="getUniqueId('infFirstName', infIndex,0)">
                                        First Name
                                    </label>
                                    <input type="text" v-bind:id="getUniqueId('infFirstName', infIndex,0)"
                                           placeholder="First Name"
                                           v-model="form.infants[infIndex].first_name" class="form-control" required>
                                </div>
                                <div class="col-sm-3 form-group">
                                    <label v-bind:for="getUniqueId('infLastName', infIndex,0)">
                                        Last Name
                                    </label>
                                    <input type="text" placeholder="Last Name"
                                           v-model="form.infants[infIndex].last_name"
                                           v-bind:id="getUniqueId('infLastName', infIndex,0)" class="form-control"
                                           required>
                                </div>
                                <div class="col-sm-2 form-group">
                                    <label v-bind:for="getUniqueId('infDob', infIndex,0)">
                                        DOB
                                    </label>
                                    <datepicker v-bind:id="getUniqueId('infDob', infIndex,0)"
                                                autocomplete="off" input-class="form-control" placeholder="DOB"
                                                :open-date="infantsDobRange.from" :disabled-dates="infantsDobRange"
                                                v-model="form.infants[infIndex].dob" @selected="getDDMMMYYYY"
                                                :format="getDDMMMYYYY" required></datepicker>
                                </div>
                                <div class="col-sm-3 form-group">
                                    <label v-bind:for="getUniqueId('infNation', infIndex,0)">
                                        Nationality
                                    </label>
                                    <select class="form-control"
                                            v-model="form.infants[infIndex].nationality"
                                            v-bind:id="getUniqueId('infNation', infIndex,0)" required>
                                        <option value="">Select</option>
                                        <option v-for="(country, countryIndexC) in countries"
                                                v-bind:value="country.displayText" :key="countryIndexC">
                                            {{country.displayText}}
                                        </option>
                                    </select>
                                </div>
                            </div>
                        </div>

                        <h2>Personal Info</h2>
                        <hr/>
                        <div class="row">
                            <div class="col-sm-4 form-group">
                                <label for="piEmail">
                                    Email
                                </label>
                                <input type="email" id="piEmail" v-model="form.email"
                                       placeholder="Email Address" class="form-control" required>
                            </div>
                            <div class="col-sm-4 form-group">
                                <label for="piConformEmail">
                                    Confirm Email
                                </label>
                                <input type="email" id="piConformEmail" v-model="form.confirm_email"
                                       placeholder="Confirm Email Address"
                                       class="form-control" required>
                            </div>
                            <div class="col-sm-4 form-group">

                                <label for="piPhoneNum">
                                    Phone Number
                                </label>
                                <div class="input-group">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text" id="basic-addon1">+92</span>
                                    </div>
                                    <input id="piPhoneNum" type="text" v-model="form.phone"
                                           placeholder="phone number" pattern="\d*" maxlength="10"
                                           class="form-control"
                                           aria-label="Username" aria-describedby="basic-addon1" required>
                                </div>
                            </div>
                        </div>
                        <h2>Other info</h2>
                        <div class="row">
                            <div class="col-sm-6 form-group">
                                <label for="piRequest">
                                    Special Request
                                </label>
                                <textarea class="form-control" maxlength="200" id="piRequest"
                                          placeholder="Special Request" rows="4"
                                          v-model="form.other"></textarea>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-sm-12">
                                <button type="submit" class="btn btn-book-now" v-bind:disabled="inProcess">Book Now</button>
                            </div>
                            <div class="col-sm-12 mt-3 dev-box">
                                <button type="button" class="btn btn-book-now" v-on:click="checkData">Check Data</button>
                            </div>
                        </div>
                    </form>
                </div>
                <div class="col-sm-12 pt-2" v-if="failed">
                    <div class="alert alert-danger"><b>Couldn't create a booking.</b>
                        <p v-html="errorMsg"></p>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
    import Datepicker from 'vuejs-datepicker'
    import {
        getDDMMMYYYY,
        addDaysToDate,
        minusDaysToDate,
        formatDate,
        getTime,
        getDifference,
        ensureTwoDigits,
        formatMoney,
        getFlightObject,
        getMinutesToString,
        getHourFromDateTimeString,
        getFlightName,
        getStopInfo,
        getLayOver,
        getCountryName,
        getUniqueId,
        getAirportName,
    } from '../api/utils.js';
    import * as DAO from "../api/apiCalls";
    import * as Sabre from '../api/Sabre.js';

    export default {
        name: 'Booking',
        components: {
            Datepicker,
        },
        data() {
            return {
                inProcess: false,
                failed: false,
                errorMsg: "",
                countries: [],
                flightData: {},
                searchData: {},
                adults: '1',
                childs: '0',
                infants: '0',
                form: {
                    adults: [],
                    childs: [],
                    infants: [],
                    email: '',
                    confirm_email: '',
                    phone: '',
                    other: '',
                },
                adultDobRange: {
                    from: minusDaysToDate(new Date(), 4381)
                },
                chilDobRange: {
                    to: minusDaysToDate(new Date(), 4380),//12 year
                    from: minusDaysToDate(new Date(), 731)
                },
                infantsDobRange: {
                    to: minusDaysToDate(new Date(), 730),
                    from: minusDaysToDate(new Date(), 1)
                }
            }
        },
        beforeMount() {
            this.countries = this.$store.getters.getCountries;
            this.flightData = this.$store.getters.getBookingFlightDetail;
            this.searchData = this.$store.getters.getUserSearch;
            if (this.searchData.adults === undefined || this.flightData.summary === undefined) {
                this.$router.push("/");
            }
            let adults = parseInt(this.searchData.adults, 10);
            let childs = parseInt(this.searchData.childs, 10);
            let infants = parseInt(this.searchData.infants, 10);
            this.adults = adults;
            this.childs = childs;
            this.infants = infants;
            for (let ac = 0; ac < adults; ac++) {
                this.form.adults.push({status: 'Mr', first_name: '', last_name: '', dob: '', nationality: ''});
            }
            for (let cc = 0; cc < childs; cc++) {
                this.form.childs.push({status: 'Mr', first_name: '', last_name: '', dob: '', nationality: ''});
            }
            for (let ic = 0; ic < infants; ic++) {
                this.form.infants.push({status: 'Mr', first_name: '', last_name: '', dob: '', nationality: ''});
            }
        },
        computed: {
            passengerInfo: function () {
                let list = [];
                let isEmpty = false;
                this.form.adults.forEach(function (o) {
                    if (o.first_name !== '' && o.last_name !== '' && o.dob !== '' && o.nationality !== '') {
                        list.push(o);
                    } else {
                        isEmpty = true;
                    }
                });
                this.form.childs.forEach(function (o) {
                    if (o.first_name !== '' && o.last_name !== '' && o.dob !== '' && o.nationality !== '') {
                        list.push(o);
                    } else {
                        isEmpty = true;
                    }
                });
                this.form.infants.forEach(function (o) {
                    if (o.first_name !== '' && o.last_name !== '' && o.dob !== '' && o.nationality !== '') {
                        list.push(o);
                    } else {
                        isEmpty = true;
                    }
                });
                if (isEmpty) {
                    return [];
                }
                return list;
            }
        },
        methods: {
            getDDMMMYYYY,
            addDaysToDate,
            minusDaysToDate,
            formatDate,
            getTime,
            getDifference,
            getMinutesToString,
            ensureTwoDigits,
            formatMoney,
            getFlightName,
            getStopInfo,
            getLayOver,
            getCountryName,
            getUniqueId,
            getAirportName,
            checkData: function(){
                let queryObject = this.$store.getters.getUserSearch;
                let segmentInfo = this.$store.getters.getBookingFlightDetail;
                console.log(JSON.stringify(queryObject));
                console.log(JSON.stringify(segmentInfo));
            },
            submitFormData: function (e) {
                e.preventDefault();
                this.failed = false;
                this.errorMsg = "";
                this.inProcess = true;
                if (this.passengerInfo.length === 0 || this.form.email === '' || this.form.confirm_email === '' || this.form.phone === '') {
                    alert("Please fill up all fields");
                    return;
                }
                if (this.form.email !== this.form.confirm_email) {
                    alert("Email address doesn't match, Please check.");
                    return;
                }
                let queryObject = this.$store.getters.getUserSearch;
                let segmentInfo = this.$store.getters.getBookingFlightDetail;
                let pnrData = {
                    "flight": queryObject,
                    "segments": segmentInfo.legs,
                    "passengers": this.passengerInfo,
                    "email": this.form.email,
                    "phone": this.form.phone,
                    "specialText": this.form.other
                };
                const requestPNR = new Promise((resolve, reject) => {
                    Sabre.createPNR(pnrData).then(function (response) {
                        resolve(response);
                    });
                });
                const okPNR = (result) => {
                    if (result.status) {
                        this.$store.commit('setPnrObj', {req: pnrData, res: result.data});
                        this.$router.push("/thanks");
                    } else {
                        this.errorMsg = result.message;
                        this.failed = true;
                    }
                    this.inProcess = false;
                };
                const failedPNR = (result) => {
                    console.info(result);
                    this.inProcess = false;
                };
                requestPNR.then(okPNR, failedPNR);
            },
        },
    }
</script>
<style lang="scss" scoped>
    @import "../scss/Booking.scss";
</style>
